import jsPDF from "jspdf";
import "jspdf-autotable";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const LaporanTitipCustomerSummary = (data) => {
  // initialize jsPDF
  const doc = new jsPDF();
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];
  let footRows = [];
  let tableColumn = [];

  tableColumn = [
    [
      {
        content: `KODE CUSTOMER`,
      },
      {
        content: `NAMA CUSTOMER`,
      },
      {
        content: `AWAL TITIP`,
      },

      {
        content: `IN TITIP`,
      },

      {
        content: `OUT TITIP`,
      },

      {
        content: `AKHIR TITIP`,
      },
    ],
  ];

  doc.setFontSize(15);
  doc.text("LAPORAN SUMMARY TITIP CUSTOMER", 14, 15);
  doc.setFontSize(10);

  data.forEach((list, index) => {
    tableColumn = [
      [
        {
          content: `JENIS TITIP : ${list.jenis_titip}`,
          colSpan: 5,
        },
      ],
      [
        {
          content: `KODE CUSTOMER`,
        },
        {
          content: `AWAL TITIP`,
        },

        {
          content: `IN TITIP`,
        },

        {
          content: `OUT TITIP`,
        },

        {
          content: `AKHIR TITIP`,
        },
      ],
    ];
    list.detail.forEach((barang, index) => {
      let rows = [
        barang.kode_customer,
        barang.awal_titip,
        barang.in_titip,
        barang.out_titip,
        barang.akhir_titip,
      ];
      tableRows.push(rows);
      footRows = [];
    });
  });
  // const date = Date().split(" ");
  // we use a date string to generate our filename.
  // const dateStr = date[2] + date[3] + date[4];
  // ticket title. and margin-top + margin-left
  doc.autoTable({
    head: tableColumn,
    body: tableRows,
    foot: footRows,
    startY: 30,
    theme: "plain",
    rowPageBreak: "avoid",
    pageBreak: "avoid",
    margin: { top: 20 },
    bodyStyles: { lineWidth: 0.02, lineColor: "#000" },
    headStyles: {
      lineWidth: 0.02,
      lineColor: "#000",
      fillColor: [212, 212, 211],
    },
  });

  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }
  doc.autoPrint();
  doc.setProperties({
    title: "LAPORAN TITIP CUSTOMER - SUMMARY",
  });
  // doc.save(`PenerimaanSUpplier.pdf`);
  var string = doc.output("datauristring");
  var x = window.open();
  x.document.open();
  x.document.write(
    "<html><head><title>LAPORAN TITIP CUSTOMER - SUMMARY</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" +
      string +
      "></embed></body></html>"
  );
  // setInterval(() => {
  //   x.close();
  // }, 1000);
};

export default LaporanTitipCustomerSummary;
