import Axios from "axios";
import React, { Component } from "react";
import { connect } from "react-redux";
import { finishSend, progressSend } from "../actions/LogicAction";
import { gagal } from "../Alert";
import { AxiosMasterGet } from "../Axios";
import { checkLaporan, getToday } from "../helper";
import FormCustomer from "./Component/FormCustomer";
import LaporanPembayaranCustomer from "./Component/LaporanPembayaranCustomer";
import LaporanPenjualanCustomer from "./Component/LaporanPenjualanCustomer";
import LaporanPiutangCustomer from "./Component/LaporanPiutangCustomer";
import LaporanPiutangCustomerCard from "./Component/LaporanPiutangCustomerCard";
import LaporanPiutangCustomerSummary from "./Component/LaporanPiutangCustomerSummary";
import LaporanPOCustomer from "./Component/LaporanPOCustomer";
import LaporanReturnCustomer from "./Component/LaporanReturnCustomer";
import LaporanTitipCustomer from "./Component/LaporanTitipCustomer";
import LaporanTitipCUstomerCard from "./Component/LaporanTitipCustomerCard";
import LaporanTitipCustomerSummary from "./Component/LaporanTitipCustomerSummary";
import LaporanTolakanCustomer from "./Component/LaporanTolakanCustomer";

const serverNonMaster = process.env.REACT_APP_BACKEND_URL_NON_MASTER;
class LaporanCustomer extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  handleGabung(data) {}
  handleHistory(data) {}
  handlePembayaran(data) {
    this.props.dispatch(progressSend());
    AxiosMasterGet(
      `report/penjualan/${data.kode_sales}&${data.tglFrom}&${data.tglTo}`
    )
      .then((res) => {
        this.props.dispatch(finishSend());
        if (checkLaporan(res.data)) {
          LaporanPenjualanCustomer(res.data);
        }
      })
      .catch((err) =>
        gagal("Gagal Mengambil data, Mohon periksa koneksi anda").then(() =>
          this.props.dispatch(finishSend())
        )
      );
  }
  handlePembayaranSales(data) {
    Axios.get(serverNonMaster + "cst/dc/pjl/details", {
      params: {
        tglFrom: data.tglFrom,
        tglTo: data.tglTo,
        kodeLokasiFrom: data.kodeLokasiFrom,
        kodelokasiTo: data.kodelokasiTo,
      },
    })
      .then((res) => {
        if (checkLaporan(res.data)) {
          localStorage.setItem(
            "LaporanPembayaranSalesCustomer",
            JSON.stringify(res.data)
          );
        }
      })
      .catch((err) =>
        gagal("Gagal Mengambil data, Mohon periksa koneksi anda")
      );
  }
  handleReturn(data) {
    this.props.dispatch(progressSend());
    AxiosMasterGet(
      `report/retur-customer/REKAP&${data.kode_customer}&${data.tglFrom}&${data.tglTo}`
    )
      .then((res) => {
        this.props.dispatch(finishSend());
        if (checkLaporan(res.data)) {
          LaporanReturnCustomer(res.data);
        }
      })
      .catch((err) =>
        gagal(`Get Data Erorr , Detial : ${err}}`).then(() =>
          this.props.dispatch(finishSend())
        )
      );
  }
  handleSaldoPiutang(data) {
    this.props.dispatch(progressSend());
    AxiosMasterGet(
      `report/bayar-piutang-customer/${data.kode_customer}&${data.tglFrom}&${data.tglTo}&REKAP&REKAP`
    )
      .then((res) => {
        this.props.dispatch(finishSend());
        if (checkLaporan(res.data)) {
          LaporanPembayaranCustomer(res.data);
        }
      })
      .catch((err) =>
        gagal("Gagal Mengambil data, Mohon periksa koneksi anda").then(() =>
          this.props.dispatch(finishSend())
        )
      );
  }
  handleTitipGr(data) {
    Axios.get(serverNonMaster + "cst/dc/ttp/details", {
      params: {
        tglFrom: data.tglFrom,
        tglTo: data.tglTo,
        kodeLokasiFrom: data.kodeLokasiFrom,
        kodelokasiTo: data.kodelokasiTo,
      },
    })
      .then((res) =>
        localStorage.setItem("LaporanTitipGrCustomer", JSON.stringify(res.data))
      )
      .catch((err) =>
        gagal("Gagal Mengambil data, Mohon periksa koneksi anda")
      );
  }
  handleTitipanPerCustomer(data) {
    switch (data.type) {
      case "SIMPLE":
        this.props.dispatch(progressSend());
        AxiosMasterGet(`report/titip-customer/${data.kode_customer}`)
          .then((res) => {
            this.props.dispatch(finishSend());
            if (checkLaporan(res.data)) {
              LaporanTitipCustomer(res.data);
            }
          })
          .catch((err) =>
            gagal(`Get Data Failed, Error : ${err}`).then(() =>
              this.props.dispatch(finishSend())
            )
          );

        break;
      case "CARD":
        this.props.dispatch(progressSend());
        AxiosMasterGet(
          `report/titip-customer-card/${data.kode_customer}&${data.jenis_transkasi}&${data.tanggal_awal}&${data.tanggal_akhir}`
        )
          .then((res) => {
            this.props.dispatch(finishSend());
            if (checkLaporan(res.data)) {
              LaporanTitipCUstomerCard(res.data);
            }
          })
          .catch((err) =>
            gagal(`Get Data Failed, Error : ${err}`).then(() =>
              this.props.dispatch(finishSend())
            )
          );

        break;
      case "SUMMARY":
        this.props.dispatch(progressSend());
        AxiosMasterGet(
          `report/titip-customer-summary/${data.kode_customer}&${data.jenis_transkasi}&${data.tanggal_awal}&${data.tanggal_akhir}`
        )
          .then((res) => {
            this.props.dispatch(finishSend());
            if (checkLaporan(res.data)) {
              LaporanTitipCustomerSummary(res.data);
            }
          })
          .catch((err) =>
            gagal(`Get Data Failed, Error : ${err}`).then(() =>
              this.props.dispatch(finishSend())
            )
          );
        break;
      default:
        break;
    }
  }

  handlePO(data) {
    this.props.dispatch(progressSend());
    AxiosMasterGet(
      `report/pre-order/${data.jenis_po}&${data.tglFrom}&${data.tglTo}`
    )
      .then((res) => {
        this.props.dispatch(finishSend());
        if (checkLaporan(res.data)) {
          LaporanPOCustomer(res.data);
        }
      })
      .catch((err) =>
        gagal(`Gagal Mengambil Data, Detial : ${err.response.data}`)
      );
  }
  handleTolakan(data) {
    this.props.dispatch(progressSend());
    AxiosMasterGet(
      `report/tolakan-customer/${data.kode_customer}&${data.tanggal_awal}&${data.tanggal_akhir}`
    )
      .then((res) => {
        this.props.dispatch(finishSend());
        if (checkLaporan(res.data)) {
          LaporanTolakanCustomer(
            data.kode_customer,
            `${data.tanggal_awal} - ${data.tanggal_akhir}`,
            "ADMIN",
            getToday(),
            "ADMIN",
            res.data
          );
        }
      })
      .catch((err) =>
        gagal(`Get Data Error , Detail : ${err}`).then(() =>
          this.props.dispatch(finishSend())
        )
      );
  }
  handlePiutang(data) {
    switch (data.type_laporan) {
      case "SIMPLE":
        this.props.dispatch(progressSend());
        AxiosMasterGet(`report/piutang-customer/${data.kode_customer}`)
          .then((res) => {
            this.props.dispatch(finishSend());
            if (checkLaporan(res.data)) {
              LaporanPiutangCustomer(res.data);
            }
          })
          .catch((err) =>
            gagal("Gagal Mengambil data, Mohon periksa koneksi anda").then(() =>
              this.props.dispatch(finishSend())
            )
          );
        break;
      case "CARD":
        this.props.dispatch(progressSend());
        AxiosMasterGet(
          `report/piutang-customer-card/${data.kode_customer}&${data.tglFrom}&${data.tglTo}`
        )
          .then((res) => {
            this.props.dispatch(finishSend());
            if (checkLaporan(res.data)) {
              LaporanPiutangCustomerCard(res.data);
            }
          })
          .catch((err) =>
            gagal("Gagal Mengambil data, Mohon periksa koneksi anda").then(() =>
              this.props.dispatch(finishSend())
            )
          );
        break;
      case "SUMMARY":
        this.props.dispatch(progressSend());
        AxiosMasterGet(
          `report/piutang-customer-summary/${data.kode_customer}&${data.tglFrom}&${data.tglTo}`
        )
          .then((res) => {
            this.props.dispatch(finishSend());
            if (checkLaporan(res.data)) {
              LaporanPiutangCustomerSummary(res.data);
            }
          })
          .catch((err) =>
            gagal("Gagal Mengambil data, Mohon periksa koneksi anda").then(() =>
              this.props.dispatch(finishSend())
            )
          );
        break;
      default:
        break;
    }
  }
  render() {
    return (
      <div className="container-fluid">
        <div className="col-lg-12">
          <div className="card">
            <div className="card-body">
              <div className="col-lg-12">
                <ul className="nav nav-tabs nav-justified nav-bordered mb-3">
                  <li className="nav-item">
                    <a
                      href="#pembayaran-tab"
                      data-toggle="tab"
                      aria-expanded="true"
                      className="nav-link active"
                    >
                      <i className="mdi mdi-account-circle d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">Penjualan</span>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      href="#pembayaranSales-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link"
                    >
                      <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">Piutang</span>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      href="#return-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link"
                    >
                      <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">Return</span>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      href="#PO-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link"
                    >
                      <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">Pre Order</span>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      href="#tolakan-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link"
                    >
                      <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">
                        Tolakan Customer
                      </span>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      href="#saldoPiutang-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link"
                    >
                      <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">
                        Pembayaran Piutang
                      </span>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      href="#titipanPerCustomer-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link"
                    >
                      <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">
                        Titipan Per Customer
                      </span>
                    </a>
                  </li>
                </ul>
                <div className="tab-content">
                  <div className="tab-pane active" id="pembayaran-tab">
                    <FormCustomer
                      from="pembayaran"
                      onSubmit={(data) => this.handlePembayaran(data)}
                    />
                  </div>
                  <div className="tab-pane" id="pembayaranSales-tab">
                    <FormCustomer
                      from="piutang"
                      onSubmit={(data) => this.handlePiutang(data)}
                    />
                  </div>
                  <div className="tab-pane" id="return-tab">
                    <FormCustomer
                      from="return"
                      onSubmit={(data) => this.handleReturn(data)}
                    />
                  </div>
                  <div className="tab-pane" id="PO-tab">
                    <FormCustomer
                      from="po"
                      onSubmit={(data) => this.handlePO(data)}
                    />
                  </div>
                  <div className="tab-pane" id="tolakan-tab">
                    <FormCustomer
                      from="tolakan"
                      onSubmit={(data) => this.handleTolakan(data)}
                    />
                  </div>
                  <div className="tab-pane" id="saldoPiutang-tab">
                    <FormCustomer
                      from="saldoPiutang"
                      onSubmit={(data) => this.handleSaldoPiutang(data)}
                    />
                  </div>
                  <div className="tab-pane" id="titipanPerCustomer-tab">
                    <FormCustomer
                      from="titipanCustomer"
                      onSubmit={(data) => this.handleTitipanPerCustomer(data)}
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect()(LaporanCustomer);
