import jsPDF from "jspdf";
import "jspdf-autotable";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const LaporanTolakanCustomer = (
  row1isi = "",
  row2isi = "",
  username = "",
  tanggal = "",
  validby = "",
  data
) => {
  // initialize jsPDF
  const doc = new jsPDF();
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];
  let footRows = [];
  let tableColumn = [];
  let finalY = 40;
  doc.setFontSize(18);
  doc.text("LAPORAN TOLAKAN CUSTOMER", 14, 15);
  doc.setFontSize(10);
  //row 1
  doc.text(`Customer : ${row1isi}`, 14, 25);
  //   row 2
  doc.text(`Tanggal	: ${row2isi}`, 120, 25);
  data.forEach((item, index) => {
    doc.text(`Kode Customer : ${item.kode_customer}`, 14, 35);
    doc.text(`Nama Customer : ${item.nama_customer}`, 100, 35);
    item.detail.forEach((list, index) => {
      tableColumn = [
        [
          {
            content: `TANGGAL : ${list.tanggal}`,
            colSpan: 11,
          },
        ],
        [
          {
            content: `NO BON : ${list.no_bon}`,
            colSpan: 11,
          },
        ],
        [
          {
            content: `NO`,
          },
          {
            content: `KODE JENIS`,
          },
          {
            content: `KADAR CETAK`,
          },
          {
            content: `KADAR BELI`,
          },
          {
            content: `QTY`,
          },
          {
            content: `P`,
          },
          {
            content: `K`,
          },
          {
            content: `PKG`,
          },
          {
            content: `GROSS`,
          },
          {
            content: `BRUTO`,
          },
          {
            content: `NETTO`,
          },
          {
            content: `ONGKOS`,
          },
        ],
      ];
      list.detail_barang.forEach((barang, index) => {
        let rows = [
          ++index,
          barang.kode_jenis,
          barang.kadar_cetak,
          barang.kadar_beli,
          barang.qty,
          barang.p,
          barang.k,
          barang.pkg,
          barang.gross,
          barang.bruto,
          barang.netto,
          parseFloat(barang.ongkos_rp).toLocaleString("id-ID"),
        ];
        tableRows.push(rows);
        console.log(tableRows);
      });
      let footer = [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        item.total_bruto,
        item.total_netto,
        parseFloat(item.total_rp).toLocaleString("id-ID"),
      ];
      footRows.push(footer);
      doc.autoTable({
        head: tableColumn,
        body: tableRows,
        foot: footRows,
        startY: index === 0 ? 40 : finalY + 4,
        theme: "plain",
        rowPageBreak: "avoid",
        pageBreak: "avoid",
        margin: { top: 20 },
        bodyStyles: { lineWidth: 0.02, lineColor: "#000" },
        headStyles: {
          lineWidth: 0.02,
          lineColor: "#000",
          fillColor: [212, 212, 211],
        },
      });
      finalY = doc.autoTableEndPosY();
      tableRows = [];
      footRows = [];
    });
  });

  doc.text(`User	: ${username}`, 14, finalY + 10);
  doc.text(`Cetak	: ${tanggal}`, 14, finalY + 16);
  doc.text(`Valid	: ${validby}`, 14, finalY + 22);
  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }
  doc.autoPrint();
  doc.setProperties({
    title: "LAPORAN TOLAKAN CUSTOMER",
  });
  // doc.save(`PenerimaanSUpplier.pdf`);
  var string = doc.output("datauristring");
  var x = window.open();
  x.document.open();
  x.document.write(
    "<html><head><title>LAPORAN TOLAKAN CUSTOMER</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" +
      string +
      "></embed></body></html>"
  );
  // setInterval(() => {
  //   x.close();
  // }, 1000);
};

export default LaporanTolakanCustomer;
