import jsPDF from "jspdf";
import "jspdf-autotable";
import { formatGram } from "../../helper";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const LaporanPiutangCustomerSummary = (data) => {
  // initialize jsPDF
  const doc = new jsPDF("landscape");
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];
  let footRows = [];
  let tableColumn = [];

  tableColumn = [
    [
      {
        content: `KODE CUSTOMER`,
      },
      {
        content: `AWAL NETTO`,
      },
      {
        content: `AWAL RP`,
      },
      {
        content: `IN NETTO`,
      },
      {
        content: `IN RP`,
      },
      {
        content: `OUT NETTO`,
      },
      {
        content: `OUT RP`,
      },
      {
        content: `AKHIR NETTO`,
      },
      {
        content: `AKHIR RP`,
      },
    ],
  ];

  doc.setFontSize(15);
  doc.text("LAPORAN PIUTANG CUSTOMER - SUMMARY", 14, 15);
  doc.setFontSize(10);
  data.forEach((barang, index) => {
    let rows = [
      barang.kode_customer,
      formatGram(barang.awal_netto),
      parseFloat(barang.awal_rp).toLocaleString("id-ID"),
      formatGram(barang.in_netto),
      parseFloat(barang.in_rp).toLocaleString("id-ID"),
      formatGram(barang.out_netto),
      parseFloat(barang.out_rp).toLocaleString("id-ID"),
      formatGram(barang.akhir_netto),
      parseFloat(barang.akhir_rp).toLocaleString("id-ID"),
    ];
    tableRows.push(rows);
  });

  doc.autoTable({
    head: tableColumn,
    body: tableRows,
    foot: footRows,
    startY: 30,
    theme: "plain",
    rowPageBreak: "avoid",
    pageBreak: "avoid",
    margin: { top: 20 },
    bodyStyles: { lineWidth: 0.02, lineColor: "#000" },
    headStyles: {
      lineWidth: 0.02,
      lineColor: "#000",
      fillColor: [212, 212, 211],
    },
  });

  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }
  doc.autoPrint();
  doc.setProperties({
    title: "LAPORAN PIUTANG CUSTOMER - SUMMARY",
  });
  // doc.save(`PenerimaanSUpplier.pdf`);
  var string = doc.output("datauristring");
  var x = window.open();
  x.document.open();
  x.document.write(
    "<html><head><title>LAPORAN PIUTANG CUSTOMER - SUMMARY</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" +
      string +
      "></embed></body></html>"
  );
  // setInterval(() => {
  //   x.close();
  // }, 1000);
};

export default LaporanPiutangCustomerSummary;
