import jsPDF from "jspdf";
import "jspdf-autotable";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const LaporanPembayaranCustomer = (data) => {
  console.log(data);
  // initialize jsPDF
  const doc = new jsPDF("landscape");
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];

  doc.setFontSize(15);
  doc.text("LAPORAN PEMBAYARAN PIUTANG CUSTOMER", 14, 15);
  doc.setFontSize(10);
  //   //row 1
  //   doc.text(`Supplier : ${row1isi}`, 14, 25);
  //   //   row 2
  //   doc.text(`Tanggal	: ${row2isi}`, 120, 25);
  data.forEach((item, index) => {
    let tableColumn = [
      [
        {
          content: `KODE CUSTOMER : ${item.kode_customer}`,
          colSpan: 2,
        },
        {
          content: `NAMA CUSTOMER : ${item.nama_customer}`,
          colSpan: 3,
        },
      ],
      [
        {
          content: `TANGGAL`,
        },
        {
          content: `NO BON`,
        },
        {
          content: `PIUTANG`,
        },
        {
          content: `BAYAR`,
        },
        {
          content: `DENDA`,
        },
        {
          content: `PUTUS TITIP`,
        },
        {
          content: `JML PIUTANG`,
        },
        {
          content: `LBH BAYAR`,
        },
      ],
    ];
    item.detail.forEach((hasil, index) => {
      let rows = [
        hasil.tanggal,
        hasil.no_bon,
        hasil.total_piutang,
        hasil.total_pembayaran,
        hasil.total_denda,
        hasil.total_putus_titip,
        hasil.jumlah_piutang,
        hasil.lebih_pembayaran,
      ];
      tableRows.push(rows);
      let detail = [
        `BAYAR CASH : ${item.detail_bayar[index].bayar_cash}`,
        `BAYAR TRANSFER : ${item.detail_bayar[index].bayar_trf}`,
        `BAYAR CIOK : ${item.detail_bayar[index].bayar_ciok}`,
        `BAYAR RONGSOK : ${item.detail_bayar[index].bayar_rongsok}`,
        `BAYAR GIRO : ${item.detail_bayar[index].bayar_giro}`,
      ];
      tableRows.push(detail);
      tableRows.push([
        {
          content: "",
          colSpan: 8,
          styles: {
            fillColor: [212, 212, 211],
          },
        },
      ]);
    });

    doc.autoTable({
      head: tableColumn,
      body: tableRows,
      startY: 30,
      theme: "plain",
      rowPageBreak: "avoid",
      pageBreak: "avoid",
      margin: { top: 20 },
      bodyStyles: { lineWidth: 0.02, lineColor: "#000" },
      headStyles: {
        lineWidth: 0.02,
        lineColor: "#000",
        fillColor: [212, 212, 211],
      },
    });
  });

  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }
  doc.autoPrint();
  doc.setProperties({
    title: "LAPORAN PEMBAYARAN PIUTANG CUSTOMER",
  });
  // doc.save(`PenerimaanSUpplier.pdf`);
  var string = doc.output("datauristring");
  var x = window.open();
  x.document.open();
  x.document.write(
    "<html><head><title>LAPORAN PEMBAYARAN PIUTANG CUSTOMER</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" +
      string +
      "></embed></body></html>"
  );
  // setInterval(() => {
  //   x.close();
  // }, 1000);
};

export default LaporanPembayaranCustomer;
